import React from 'react';
import ReactDOM from 'react-dom';
import Modal from 'react-modal';
import { useForm } from 'react-hook-form';
import HeaderWhite from './header-white';
import DeliverySmallForm from './delivery-small-form';
import hero from './heroNoCourier.svg';
import dpdSkew from './couriers/dpdSkew.png';
import nemoSkew from './couriers/nemoSkew.png';
import urgentCargusSkew from './couriers/urgentCargusSkew.png';
import samedaySkew from './couriers/samedaySkew.png';
import dragonStarSkew from './couriers/dragonStarSkew.png';
import './App.css';

const customStyles = {
  content : {
    top                   : '50%',
    left                  : '50%',
    right                 : 'auto',
    bottom                : 'auto',
    marginRight           : '-50%',
    transform             : 'translate(-50%, -50%)'
  }
};
 
// Make sure to bind modal to your appElement (http://reactcommunity.org/react-modal/accessibility/)
Modal.setAppElement('body')

function App() {
  const emailForm = useForm();
  const feedbackForm = useForm();
  
  const [modalIsOpen, setIsOpen] = React.useState(false);
  function closeModal(){
    setIsOpen(false);
  }

  function submitForm(data) {
    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(data)
    };
    fetch('https://www.formingo.co/submit/1e1fa4ec-75a4-4e48-986f-b48942a5b672', requestOptions)
      .then(response => response.json())
      .then(data => setIsOpen(false));
  }

  return (
    <div className="App">
      <HeaderWhite onClick={() => setIsOpen(true)}/>
      <section class="text-white body-font">
        <div class="container px-5 pt-12 mx-auto flex flex-wrap">
          <div class="md:w-1/2 md:pr-16 md:pt-12 lg:pr-0 pr-0">
            <h1 class="title-font font-medium text-3xl text-white">
              Livrează orice<br/>
              La cel mai bun preț din țară
            </h1>
            <p class="leading-relaxed mt-4">
              Costurile practicate de curieri sunt prea mari?<br/>
              Am negociat cel mai avantajos cost pentru tine.
            </p>
            <p class="leading-relaxed">
              <form onSubmit={emailForm.handleSubmit(submitForm)} class="flex items-start">
                <div class="md:w-1/2 mr-4 mb-4 sm:mb-0">
                  <input class="md:w-full mt-4 bg-gray-100 rounded border border-gray-400 focus:outline-none focus:border-gray-500 text-gray-900 text-base px-4 py-2" name="email" placeholder="Email"
                    ref={emailForm.register({ 
                      required: "Camp obligatoriu.",
                      pattern: {
                        value: /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
                        message: "Adresa de email invalida."
                      }})
                    } />
                    {emailForm.errors.email && <p class="text-red-500 text-xs italic">{emailForm.errors.email.message}</p>}
                </div>
                <button type="submit" class="mt-4 text-white bg-red-500 border-0 py-2 px-8 focus:outline-none hover:bg-red-600 rounded text-lg">Abonează-te</button>
              </form>
            </p>
          </div>
          <div class="md:w-1/2 px-8 flex flex-col md:ml-auto w-full mt-10 md:mt-0">
            <div class="w-100 relative">
              <img class="w-full" src={hero} alt="Fabrica de Colete"></img>
              <img class="w-full" src={dpdSkew} class="absolute courierSkew dpdSkew"></img>
              <img class="w-full" src={nemoSkew} class="absolute courierSkew nemoSkew"></img>
              <img class="w-full" src={urgentCargusSkew} class="absolute courierSkew urgentCargusSkew"></img>
              <img class="w-full" src={samedaySkew} class="absolute courierSkew samedaySkew"></img>
              <img class="w-full" src={dragonStarSkew} class="absolute courierSkew dragonStarSkew"></img>
            </div>
          </div>
        </div>
      </section>
      <DeliverySmallForm onClick={() => setIsOpen(true)} />

      <Modal
          isOpen={modalIsOpen}
          onRequestClose={closeModal}
          style={customStyles}
          overlayClassName={ "fixed top-0 right-0 bottom-0 left-0 bg-black bg-opacity-75"}
          contentLabel="Example Modal">
          <div class="text-gray-900">
            <p class="title-font font-medium text-xl">Spune-ne opinia ta!</p>
            <p class="leading-relaxed mt-3">
              Dupa cum poti observa, platforma este inca in lucru. <br />
              Suntem foarte recunoscatori pentru orice ajutor, asa ca nu rata ocazia sa-ti pui amprenta. <br />
            </p>
            <p class="leading-relaxed mt-3">
              Care sunt asteptarile tale si ce functionalitati crezi ca te-ar ajuta pe tine sau afacerea ta?
            </p>
            <form onSubmit={feedbackForm.handleSubmit(submitForm)}>
              <textarea class="mt-3 shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none" name="feedback" placeholder="Opinia ta" rows="5" 
                ref={feedbackForm.register({ 
                  required: "Camp obligatoriu.",
                  minLength: {
                    value: 5,
                    message: "Sunt necesare cel putin 5 caractere."
                  }
                })
                } ></textarea>
              {feedbackForm.errors.feedback && <p class="text-red-500 text-xs italic">{feedbackForm.errors.feedback.message}</p>}
              <div class="mt-3 text-right">
                <button type="submit" class="text-white bg-red-500 border-0 py-2 px-8 focus:outline-none hover:bg-red-600 rounded text-lg">Gata</button>
              </div>
            </form>
          </div>
      </Modal>
    </div>
  );
}

export default App;
