import React from 'react'
import logo from './logo-white.svg';

export default function HeaderWhite(props) {
  return (
    <div>
      <header class="text-white body-font">
        <div class="container mx-auto flex flex-wrap p-5 flex-col md:flex-row items-center">
          <a class="flex title-font font-medium items-center mb-4 md:mb-0">
            <img src={logo} alt="Fabrica de Colete"></img>
          </a>
          <nav class="md:ml-auto flex flex-wrap items-center text-base justify-center">
            <a class="mr-5 cursor-pointer hover:underline" onClick={props.onClick}>Servicii</a>
            <a class="mr-5 cursor-pointer hover:underline" onClick={props.onClick}>Prețuri</a>
            <a class="mr-5 cursor-pointer hover:underline" onClick={props.onClick}>Urmărește Livrarea</a>
          </nav>
          <button onClick={props.onClick} class="inline-flex items-center border border-white py-1 px-3 focus:outline-none rounded-full text-base mt-4 md:mt-0">
            Intră în Cont
          </button>
        </div>
      </header>
    </div>
  )
}
