import React from 'react'
import checkIcon from './icons/md-check-circle.svg';

export default function DeliverySmallForm(props) {
  return (
    <section class="opacity-25 text-gray-700 body-font" onClick={props.onClick}>
      <div class="container px-5 py-24 mx-auto">
        <div class="flex flex-wrap -m-4">
          <div class="p-4 w-full">
            <div class="shadow-lg bg-white px-4 py-6 rounded-lg">
              <div class="mb-4 flex flex-wrap">
                <div class="lg:w-3/7 sm:w-2/5 w-full px-2">
                  <label class="block text-gray-700 text-sm font-bold mb-2" for="fromAddress">
                    Adresa de Colectare
                  </label>
                  <input class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none" id="fromAddress" type="text" placeholder="Ex: Cluj-Napoca" />
                </div>
                <div class="lg:w-3/7 sm:w-2/5 w-full px-2">
                  <label class="block text-gray-700 text-sm font-bold mb-2" for="toAddress">
                    Adresa de Livrare
                  </label>
                  <input class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none" id="toAddress" type="text" placeholder="Ex: Buzău" />
                </div>
                <div class="lg:w-1/7 sm:w-1/5 w-full px-2">
                  <label class="block text-gray-700 text-sm font-bold mb-2" for="weight">
                    Greutate
                  </label>
                  <div class="bg-white flex flex-wrap items-stretch items-center shadow border rounded w-full py-2 px-3 leading-tight">
                    <input class="flex-shrink flex-grow flex-auto flex-1 w-px self-center relative appearance-none text-gray-700 leading-tight focus:outline-none" id="weight" type="text" placeholder="2" />
                    <div class="flex -mr-px justify-center w-15 px-4">
                      <span class="text-gray-700 text-sm font-bold">Kg</span>
                    </div>
                  </div>
                </div>
              </div>
              <div class="flex items-center justify-between px-2">
                <div>
                  <div class="inline-block mr-5">
                    <img src={checkIcon} class="inline-block" />
                    <span class="text-gray-700 text-sm font-medium ml-1">Ramburs</span>
                  </div>
                  <div class="inline-block mr-5">
                    <img src={checkIcon} class="inline-block" />
                    <span class="text-gray-700 text-sm font-medium ml-1">Deschidere la livrare</span>
                  </div>
                  <div class="inline-block mr-5">
                    <img src={checkIcon} class="inline-block" />
                    <span class="text-gray-700 text-sm font-medium ml-1">Printarea AWB-ului nu este obligatorie</span>
                  </div>
                </div>
                <button class="text-white bg-red-500 border-0 py-2 px-8 focus:outline-none hover:bg-red-600 rounded text-lg">Trimite</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  )
}
